<template>
<div>
  <my-logo title="博客列表"></my-logo>
  <p>页面上的值：</p>
  <p>通过视图层，修改my_value:</p>
  <input v-model="my_value" style="width:400px" />
  <hr />
  <input type="button" value="通过控制代码修改my_value" @click="change_my_value_code()" />
  <hr />
  <input type="button" value="显示代码中的my_value" @click="show_my_value" />
  <hr />
  <h1>数据双向绑定</h1>
  input:<input type="number" v-model='input_value' /><br />
  输入的值:{{ input_value }}<br />
  text_area:<textarea v-model="textarea_value"></textarea><br />
  输入的值:{{ textarea_value }}<br />
  radio:
  <input type="radio" v-model="radio_value" value="A" />A,
  <input type="radio" v-model="radio_value" value="B" />B,
  <input type="radio" v-model="radio_value" value="C" />C,
  <br />
  输入的值:{{ radio_value }}
  <br />
  checkbox:
  <input type="checkbox" v-model='checkbox_value' v-bind:true-value='true' v-bind:false-value='false' /><br />
  输入的值:{{ checkbox_value }}
  <br />
  select：
  <select v-model='select_value'>
    <option v-for='e in options' v-bind:value='e.value' :key='e.value'>{{e.text}}</option>
  </select><br />
  输入的值:{{select_value}}
  <br />
</div>
</template>

<script>
import MyLogo from '@/components/Logo'
export default {
  data () {
    return {
      my_value: '默认值',
      input_value: 12,
      textarea_value: '使用说明',
      radio_value: '',
      checkbox_value: true,
      select_value: 'C',
      options: [{
        'text': '红烧肉',
        'value': 'A'
      },
      {
        'text': '香包肉',
        'value': 'B'
      },
      {
        'text': '水煮肉',
        'value': 'C'
      }
      ]
    }
  },
  // 生命周期 - 创建完成（访问当前this实例）
  created () {},
  // 生命周期 - 挂载完成（访问DOM元素）
  mounted () {},
  methods: {
    show_my_value: function () {
      alert('my_value:' + this.my_value)
    },
    change_my_value_code: function () {
      this.my_value += ',在代码中做修改，6666'
    }
  },
  components: {
    MyLogo
  }
}
</script>

<style scoped>
/* @import url(); 引入css类 */
</style>



