<template>
  <div id="div">
    <my-logo style="color:blue"  :likes="23" name="title2" title="sdwe" v-on:listenChildEvent='showMsgFromChild'>
<template v-slot:mySlot="slotProps">
        <span>{{slotProps.user}}</span>
    </template></my-logo>
    <router-link :to="{ name: 'Blog_detail', query: { id: 3 } }"
      >查询详细信息</router-link
    >
    <button @click="show_blog(3)">查询详细信息</button>
    <h1>{{ this.$store.state.a.province }}</h1>
    <h1>{{ this.$store.state.b.province }}</h1>
    <button @click="show_Vuex()">Vuex</button>
    <input type="text" v-mydirective v-model='title2'  id="username"/>
    <button @click="add_input()">添加侦听</button>
     <button @click="delete_input()">删除侦听</button>
  </div>
</template>
<script>
import MyLogo from '@/components/Logo'
/* eslint-disable */
export default {
  data () {
    return {
      true: false,
      title2: 'hello,第一个程序2',
      message: false,
      hobbyList: ['爬山', '跑步', '唱歌'],
      age: 15,
      name: 'czf',
      person1: {
        name: '张三',
        age: 18,
        address: '北京昌平'
      },
      user: {
        name: '张三',
        age: 18
      },
      kilometers: 0,
      meters: 0
    }
  },
  methods: {
    showMsgFromChild: function (data) {
      console.log(data)
    },
    sayHello: function () {
      alert('hello,vue.js')
    },
    show_blog: function (blogID) {
      console.log(blogID)
      this.$router.push({
        name: 'Blog_detail',
        query: {
          id: blogID
        }
      })
    },
    show_Vuex: function () {
      // this.$store.commit('edit', {'name': 'afeng2', 'age': 15})
      // this.$store.dispatch('aEdit', {age: 15})
      console.log(this.$store.state.a)
    },
    a:function(){
     console.log("a")
    },
     b:function(){
     console.log("b")
    },
    add_input:function(){
    },
    delete_input:function(){
     document.getElementById("username").removeEventListener('click',this.b,false)
    }
  },
  watch: {
    kilometers: function (val) {
      this.kilometers = val
      this.meters = val * 1000
    },
    meters: function (val) {
      this.kilometers = val / 1000
      this.meters = val
    }
  },
  components: {
    MyLogo: MyLogo
  },
  // 生命周期 - 创建完成（访问当前this实例）
  created () {},
  // 生命周期 - 挂载完成（访问DOM元素）
  mounted () {
    console.log(this.$el.children[0].getAttribute('name'))
  }
}
</script>
<style lang="scss" scoped>
$bgColor:red;
h1{
  color:$bgColor
}
</style>



