import Vue from 'vue'
import Vuex from 'vuex'
// 挂载Vuex
Vue.use(Vuex)
// 创建VueX对象
const moduleA = {
  state: {
    'province': '湖南'
  },
  mutations: {},
  actions: {},
  getters: {}
}
const moduleB = {
  state: {
    'province': '北京'
  },
  mutations: {},
  actions: {}
}
const store = new Vuex.Store({
  modules: {
    a: moduleA,
    b: moduleB
  }
}
)
export default store



