<template>
  <div>
    {{ hi("from view") }}
  </div>
</template>

<script>
import CommonHi from '@/mixins/common_hi.js'
export default {
  mixins: [CommonHi],
  // 生命周期 - 挂载完成（访问DOM元素）
  mounted () {
    alert(this.hi('from script from'))
  }
}
</script>
<style scoped>
/* @import url(); 引入css类 */
</style>



