<template>
  <div>
    <table border="1">
      <tr>
        <td>ID</td>
        <td>title</td>
        <td>created_at</td>
      </tr>
      <tr>
        <td>{{ blog.id }}</td>
        <td>{{ blog.title }}</td>
        <td>{{ blog.created_at }}</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      blog: {}
    }
  },
  // 生命周期 - 创建完成（访问当前this实例）
  created () {},
  // 生命周期 - 挂载完成（访问DOM元素）
  mounted () {
    // console.log(this.$ajax)
    var that = this
    this.$ajax({
      url: 'api/interface/blogs/show?id=' + this.$route.query.id,
      //  url: 'api/interface/blogs/show/' + this.$route.params.id,
      //  url: 'data/user',
      method: 'get'
    }).then(function (data2) {
      console.log(data2.data.result.title)
      that.blog = data2.data.result
    })
  }
}
</script>

<style scoped>
/* @import url(); 引入css类 */
</style>



