<template>
<div>
  <h1>{{title}}</h1>
  <table border="1">
    <tr>
      <td>
        ID
      </td>
      <td>
        title
      </td>
      created_at
      <td>
        操作
      </td>
    </tr>
    <tr v-for="blog in blogs" :key="blog.id">
      <td>{{blog.id}}</td>
      <td>{{blog.title}}</td>
      <td>{{blog.created_at}}</td>
      <!--<td @click="show_blog(blog.id)">
        查询详细信息
      </td>-->
      <td><router-link :to="{name:'Blog_detail',query:{id:blog.id}}">查询详细信息</router-link></td>
    </tr>
  </table>
</div>
</template>

<script>
export default {
  data () {
    return {
      title: '博客列表页',
      blogs: [

      ]
    }
  },
  methods: {
    show_blog: function (blogID) {
      console.log(blogID)
      this.$router.push({
        name: 'Blog_detail',
        query: {
          'id': blogID
        }
      })
    }
  },
  // 生命周期 - 创建完成（访问当前this实例）
  created () {},
  // 生命周期 - 挂载完成（访问DOM元素）
  mounted () {
    // console.log(this.$ajax)
    var that = this
    this.$ajax({
      url: 'api/interface/blogs/all',
      //  url: 'data/user',
      method: 'get'
    }).then(function (data2) {
      console.log(data2.data)
      that.blogs = data2.data.blogs
    })
  }
}
</script>

<style scoped>
/* @import url(); 引入css类 */
a{
  text-decoration:none;
}
</style>



