<template>
  <div id="app">
    <b-child
      :p-child1="child1"
      :p-child2="child2"
      v-on:test1="onTest1"
     v-on:upRocket="reciveRocket">
    </b-child>
  </div>
</template>
<script>
import B from './B.vue'
export default {
  data () {
    return {
      child1: 'A组件传递给B组件的数据:child1',
      child2: 'A组件传递给C组件的数据:child2'
    }
  },
  components: { BChild: B },
  methods: {
    onTest1 (data) {
      console.log('test1 running...' + data)
    },
    reciveRocket (data) {
      console.log('reciveRocket success ,' + data)
    }
  }
}
</script>



