<template>
  <div>
    <h1>{{ hello }}</h1>
    <table border="1">
      <tr>
        <td>name</td>
        <td>password</td>
      </tr>
      <tr v-for="name in ajax_text" :key="name['username']">
        <td>
          {{ name["username"] }}
        </td>
        <td>
          {{ name["userpwd"] }}
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      hello: 'hello,ajax！czf',
      ajax_text: []
    }
  },
  mounted () {
    // console.log(this.$ajax)
    var that = this
    this.$ajax({
      // url: 'http://localhost:3000/api/user',
      url: 'data/user',
      method: 'get'
    }).then(function (data2) {
      that.ajax_text = data2.data
    })
  }
}
</script>

<style></style>



