<template>
<div style="color:red">
<h1>{{title}}</h1>
<ul>
<li v-for="(item, index) in commentIds" :key="index">
     {{item}}
</li>
</ul>
<h2>{{likes}}</h2>
<h3>{{author.name}}</h3>
<h3>{{author.age}}</h3>
<h3>{{author.address}}</h3>

  <div class='logo'>
    <img src='http://siwei.me/assets/template/header-4cf66dc6e4e154c259c682cf425d30f0.png' />
  </div>
  <button v-on:click='sendMsgToParent'>向父组件传值</button>
   <button> <slot v-bind:user="user">{{user.name}}</slot>  </button>
</div>
</template>

<script>
export default {
  data () {
    return {
      user: {
        name: 'afeng'
      }
    }
  },
  inheritAttrs: false,
  props: {
    title: {
      type: String,
      default: 'hello,vue componebt!',
      required: true
    },
    likes: {
      type: Number,
      default: 34
      //  required: true
    },
    commentIds: {
      type: Array,
      default: () => {
        return [23, 45, 67]
      }
    },
    author: {
      type: Object,
      default: () => (
        {
          name: '张三2',
          age: 19,
          address: '北京昌平2'
        }
      )
    }

  },
  methods: {
    sendMsgToParent: function () {
      this.$emit('listenChildEvent', 'this message from child')
    }
  },
  mounted () {
    console.log(this.$el.style.color)
  }
}
</script>

<style scoped>
/* @import url(); 引入css类 */
.logo {
  border: 1px solid red
}
</style>



