<template>
  <div class="child-2">
    <p>in C-child2:</p>
    <p>props: {{ pChild2 }}</p>
    <p>$attrs: {{ $attrs }}</p>
    <button @click="startUpRocket">我要发射火箭</button>
  </div>
</template>
<script>
export default {
  props: ['pChild2'],
  data () {
    return {}
  },
  inheritAttrs: false,
  mounted () {
    this.$emit('test2')
  },
  methods: {
    startUpRocket () {
      this.$emit('upRocket', 'C组件向A组件发送的数据')
      console.log('startUpRocket')
    }
  }
}
</script>



