<template>
<div>
  <my-logo :title='title'></my-logo>
  <textarea v-model="content"></textarea><br />
  <input type="button" @click="submit" value="留言" />
</div>
</template>

<script>
import MyLogo from '@/components/Logo'
export default {

  data () {
    return {
      content: 'we2',
      title: '数据提交'
    }
  },
  // 生命周期 - 创建完成（访问当前this实例）
  created () {

  },
  // 生命周期 - 挂载完成（访问DOM元素）
  mounted () {

  },
  methods: {
    submit: function () {
      var that = this
      this.$ajax({
        url: 'api/interface/blogs/add_comment',
        method: 'post',
        data: {
          content: that.content
        }
      }).then(function (data2) {
        console.log(data2.data)
        that.content = '发送成功：' + data2.data.content
      }, function (error) {
        console.log(error)
      })
    }
  },
  components: {
    MyLogo
  }
}
</script>

<style scoped>
/* @import url(); 引入css类 */
</style>



