import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/hello'
import AjaxDemo from '@/components/AjaxDemo'
import Blog from '@/components/Blog'
import BlogDetail from '@/components/Blog_detail'
import TwoWayBinding from '@/components/TwoWayBinding'
import FormSubmit from '@/components/FormSubmit'
import SayHifromMixins from '@/components/SayHifromMixin'
import A from '@/components/A'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/hello',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/ajax',
      name: 'AjaxDemo',
      component: AjaxDemo
    },
    {
      path: '/blog',
      name: 'Blog',
      component: Blog
    },
    {
      path: '/blog_detail',
      name: 'Blog_detail',
      component: BlogDetail
    },
    {
      path: '/two_way_binding',
      name: 'TwoWayBinding',
      component: TwoWayBinding
    },
    {
      path: '/form_submit',
      name: 'FormSubmit',
      component: FormSubmit
    },
    {
      path: '/say_hi_from_mixin',
      name: 'SayHifromMixins',
      component: SayHifromMixins
    },
    {
      path: '/a',
      name: 'a',
      component: A
    }
  ]
})



